body {
    margin: 0;
    /* font-size: 28px; */
    font-family: Arial, Helvetica, sans-serif;
    overflow-x: hidden;
  }
  
  .header {
    background-color: #f1f1f1;
    padding: 30px;
    text-align: center;
  }
  
  #navbar {
    overflow: hidden;
    /* background-color: #333; */
    /* background-color: transparent; */ 
    background-color: rgb(16, 211, 23);
    /* position: fixed;
     */
     /* position: fixed; */
     z-index:99;
     /* display: none; */
  }
  
  #navbar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  #navbar a:hover {
    /* background-color: #ddd; */
    /* color: black; */
  }
  
  #navbar a.active {
    background-color: #4CAF50;
    color: white;
  }
  
  .content {
    /* padding: 16px; */
  }
  
  .sticky {
    position: fixed;
    top: 0;
    width: 100%;
  }
  
  .sticky + .content {
    padding-top: 60px;
  }


.wrapper {
  position: relative;
}

.text{
  /* background-color: black; */
  color: rgb(245, 238, 238);
  position: absolute;
  /* text-align: center; */
  /* top: 40%; */
  width: 100%;
}


.removeA{
    display: none !important;
    visibility: hidden!important;
    transform: scale(0);
}

.backgroundColor{
  background: linear-gradient(to right, #FD746C, #2C3E50);
}


@media (max-width: 645px) { 
  /* for mobile version remove  */
  #navbar{
    display: none;
  }
}